import React from 'react';
import {useNavigate} from "react-router-dom";
import {setCategoryId, setCurrentPage} from "../redux/filter/slice";
import {selectPizzaData} from "../redux/pizza/selectors";
import {useAppDispatch, useAppSelector} from "../redux/store";
import {selectFilter} from "../redux/filter/selectors";
import {fetchPizzas} from "../redux/pizza/asyncActions";
import {PizzaBlock, PizzaBlockProps} from "../components/pizzaBlock/PizzaBlock";
import {Skeleton} from "../components/pizzaBlock/Skeleton";
import {Categories} from "../components/cart/Categories";
import {Sort} from "../components/sort/Sort";
import {Pagination} from "../components/pagination/Pagination";


export const Home: React.FC = () => {
    const navigate = useNavigate()
    const dispatch = useAppDispatch()
    const isMounted = React.useRef(false)

    const {categoryId, sort, currentPage, searchValue} = useAppSelector(selectFilter)
    const {items, status} = useAppSelector(selectPizzaData)

    const onChangeCategory = React.useCallback((id: number) => {
        dispatch(setCategoryId(id))
    }, [])

    const onChangePage = (page: number) => {
        dispatch(setCurrentPage(page))
    }

    const getPizzas = async () => {

        const sortBy = sort.sortProperty.replace('-', '')
        const order = sort.sortProperty.includes('-') ? 'asc' : 'desc';
        const category = categoryId > 0 ? `category=${categoryId}` : ''
        const search = searchValue ? `&search=${searchValue}` : ''

        dispatch(fetchPizzas({
            sortBy,
            order,
            category,
            search,
            currentPage: String(currentPage)
        }))

        window.scrollTo(0, 0)
    }

    //       
    /*React.useEffect(() => {
        if (isMounted.current) {
            const queryString = qs.stringify({
                sortProperty: sort.sortProperty,
                categoryId,
                currentPage,
            })
            navigate(`?${queryString}`)
        }
        isMounted.current = true

        if (window.location.search) {
            dispatch(fetchPizzas({} as SearchPizzaParams))
        }
    }, [categoryId, sort.sortProperty, searchValue, currentPage])*/

    //    ,   URL-    Redux
    /*React.useEffect(() => {
        return () => {
            if (window.location.search) {
                const params = qs.parse(window.location.search.substring(1)) as unknown as SearchPizzaParams;
                const sort = sortList.find(obj => obj.sortProperty === params.sortBy)
                dispatch(setFilters({
                    searchValue: params.search,
                    categoryId: Number(params.category),
                    currentPage: Number(params.currentPage),
                    sort: sort || sortList[0],
                }))
            }
            isSearch.current = true
        }
    }, [])*/

    //    ,   
    React.useEffect(() => {
        // window.scrollTo(0, 0)
        // if (!isSearch.current) {
        getPizzas()
        // }
        // isSearch.current = false
    }, [categoryId, sort.sortProperty, searchValue, currentPage])

    const pizzas = items.map((obj: JSX.IntrinsicAttributes & PizzaBlockProps) => <PizzaBlock key={obj.id} {...obj}/>)

    const skeletons = [...new Array(6)].map((_, index) => <Skeleton key={index}/>)

    return (
        <div className="container">
            <div className="content__top">
                <Categories
                    value={categoryId}
                    onChangeCategory={onChangeCategory}
                />
                <Sort value={sort}/>
            </div>
            <h2 className="content__title"> </h2>
            {
                status === 'error'
                    ? <div className={'content__error-info'}>
                        <h2>  </h2>
                        <p> ,    .  
                             .</p>
                    </div>
                    : <div className="content__items">
                        {status === 'loading' ? skeletons : pizzas}
                    </div>
            }
            <Pagination currentPage={currentPage} onChangePage={onChangePage}/>
        </div>
    );
};

